.slider-left {
  width: 34px;
  height: 28px;
  position: absolute;
  top: 50%;
  left: -22px;
  z-index: 100;
  border-radius: 4px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.06);

  @media screen and (min-width: 1280px) {
    width: 48px;
    height: 40px;
    left: -40px;
    border-radius: 8px;
  }
}

.slider-right {
  width: 34px;
  height: 28px;
  position: absolute;
  top: 50%;
  right: -22px;
  z-index: 100;
  border-radius: 4px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.06);

  @media screen and (min-width: 1280px) {
    width: 48px;
    height: 40px;
    right: -40px;
    border-radius: 8px;
  }
}
